import React, { Component } from "react";
import banner1 from "../img/banner1.jpg";
import cloud from "../img/cloud.png";
import { motion } from "framer-motion";

class Container1 extends Component{

  render(){
    return(

      <div className="md:p-28 ">
          <motion.img initial={{x:0}} animate={{x:"5vw"}} transition={{ duration: 4,yoyo:Infinity}} src={cloud} className="hidden md:block w-2/12 absolute z-0 top-1/3 z-20 right-1/2" alt="Cloud" />
          <img src={banner1} className="py-20" alt="Banner1" />
          <motion.img initial={{x:0}} animate={{x:"5vw"}} transition={{ duration: 8,yoyo:Infinity}} src={cloud} className="hidden md:block w-2/12 absolute z-0 top-2/3 z-20 right-2/2" alt="Cloud" />
      </div>

    );
  }
}

export default Container1;