import React, { Component } from "react";
import cloud from "../img/cloud.png";
import saloka from "../img/logo-saloka.png";
import { motion } from "framer-motion"

class Loading extends Component{  
    render(){
      return(
  
        <div className="w-full h-screen bg-gray-900 relative overflow-hidden">
            <motion.img initial={{x:0}} animate={{x:"-12vw",opacity:0}} transition={{ duration: 4}} src={cloud} className="hidden md:block w-5/12 absolute top-1/3 z-20 right-1/2" alt="Cloud" />
            <motion.img initial={{opacity:0}} animate={{opacity:1}} transition={{ duration: 6 }} src={saloka} className="md:w-3/12 w-7/12 relative m-auto py-52 z-10" alt="Saloka" />
            <motion.img initial={{x:0}} animate={{x:"13vw",opacity:0}} transition={{ duration: 4}} src={cloud} className="hidden md:block w-5/12 absolute top-1/4 z-20 left-1/2" alt="Cloud"/>
        </div>
  
      );
    }
  }
  
  export default Loading;