import React, { Component } from "react";
import Container1 from "./components/Container1";
import Footer from "./components/Footer";
import Loading from "./components/Loading";
import Navbar from "./components/Navbar";

class App extends Component{

  constructor(props){
    super(props);
    this.state = {
      status : false
    }
  }

  componentDidMount(){
    setTimeout(() => this.setState({status:true}),4500)
  }

  render(){
    return(

      <>
        {
          this.state.status ?
          <>
            <Navbar />
            <div className="overflow-hidden cursor-pointer z-20 fixed bg-green-600 md:rounded-full animate-pulse bottom-10 right-6 rounded-sm">
              <span className="absolute top-1 animate-ping inline-flex right-0 w-3 h-3 bg-yellow-700 rounded-full"></span>
              <h1><i class='bx bxl-whatsapp text-3xl md:text-5xl text-white'></i></h1>
            </div>
            <Container1 />
            <Footer />
          </>
          :
            <Loading />
        }
      </>

    );
  }
}

export default App;