import React, { Component } from "react";
import footer from "../img/footer.svg";
import tirex from "../img/tirex.png";
import saloka from "../img/logo-saloka.png";

class Footer extends Component{
  
    render(){
      return(
        
        <div className="bottom-0 relative">
            <img src={footer} className="relative z-10" alt="footer"/>
            <img src={tirex} className="w-2/12 absolute animate-bounce top-0 z-0" alt="tirex"/>
            <div className="flex justify-center">
              <img src={saloka} className="w-2/12 absolute inset-y-48 z-20" alt="salka"/>
            </div>
            <div className="flex justify-center items-center bg-gray-900 text-white font-Montserrat">
              <div className="p-5 w-4/12 font-bold">
                <p className="leading-8 tracking-wide">
                SALOKA Theme Park <br/>
                Jl. Fatmawati No.154, Tuntang, Semarang, Jawa Tengah 50773
                </p>
              </div>
              <div className="p-5 w-4/12">
                <input type="text" value="" className="border-0 p-2 focus:outline-none" placeholder="Masukkan Email"/>
                <button className="p-2 bg-yellow-600 m-1 hover:bg-yellow-700"><i class='bx bx-send' ></i></button>
              </div>
            </div>
        </div>

      );
    }
  }
  
  export default Footer;