import React, { Component } from "react";
import saloka from "../img/logo-saloka.png";
import { motion } from "framer-motion";

class Navbar extends Component{  

    constructor(props){
        super(props);
        this.state = {
            status : false
        }
    }

    render(){
    const modeSun = () => this.setState({status:true});
    const modeMoon = () => this.setState({status:false});
      return(
        <nav class="bg-gray-900 p-4 fixed flex z-30 items-center cursor-pointer justify-between">
            <img src={saloka} className="w-2/12" alt="Saloka"/>

            <div className="flex">
                <motion.h6 whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="text-gray-100 text-lg font-Montserrat px-10 font-bold hover:text-gray-200">Zona</motion.h6>
                <motion.h6 whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="text-gray-100 text-lg font-Montserrat px-10 font-bold hover:text-gray-200">Hiburan</motion.h6>
                <motion.h6 whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="text-gray-100 text-lg font-Montserrat px-10 font-bold hover:text-gray-200">Kuliner</motion.h6>
                <motion.h6 whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="text-gray-100 text-lg font-Montserrat px-10 font-bold hover:text-gray-200">Fasilitas</motion.h6>
                <motion.h6 whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="text-gray-100 text-lg font-Montserrat px-10 font-bold hover:text-gray-200">Tiket</motion.h6>
            </div>
            
            <div>
                {
                    this.state.status ?
                        <motion.button whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="bg-white py-1 px-2" onClick={modeMoon}>
                            <i className='bx bxs-moon text-2xl' ></i>
                        </motion.button>
                    :
                        <motion.button whileHover={{scale:1.1}} transition={{type:"spring",stiffness : 600}} className="bg-white py-1 px-2" onClick={modeSun}>
                            <i className='bx bxs-sun text-2xl text-yellow-500' ></i>
                        </motion.button>
                }
            </div>
        </nav>
  
      );
    }
  }
  
  export default Navbar;